import React from "react";
import key from "../../../util/key";
import Context from "../../../Context";
import Flex from "../../../util/Flex";
import {
  DeleteOutlined,
  UserOutlined,
  UsergroupAddOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import UserGroupPicker from "../../../util/UserGroupPicker";

import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import {
  Button,
  Space,
  Spin,
  message,
  Alert,
  Table,
  Popconfirm,
  Typography,
  Row,
  Col,
} from "antd";

const { Text } = Typography;

const accessRights = {
  class: {
    deny: {
      ACCESS: parseInt("10000", 2),
      CREATE: parseInt("01000", 2),
      SELECT: parseInt("00100", 2),
      UPDATE: parseInt("00010", 2),
      DELETE: parseInt("00001", 2),
    },
    allow: {
      ACCESS: parseInt("10000", 2),
      CREATE: parseInt("01000", 2),
      SELECT: parseInt("00100", 2),
      UPDATE: parseInt("00010", 2),
      DELETE: parseInt("00001", 2),
    },
  },
  method: {
    deny: {
      EXECUTE: parseInt("100", 2),
      VISIBLE: parseInt("010", 2),
      ENABLE: parseInt("001", 2),
    },
    allow: {
      EXECUTE: parseInt("100", 2),
      VISIBLE: parseInt("010", 2),
      ENABLE: parseInt("001", 2),
    },
  },
};

class WorkflowSecurity extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      formKey: key(),
      savePending: false,
      pending: false,
      pick: false,
      items: [],
      item: {},
    };

    this.apiPath = false;
    if (this.props.mode === "class") {
      this.apiPath = "/workflow/class/access";
      this.defaultAddMask = 16;
      this.bitsCount = 5;
    }
    if (this.props.mode === "method") {
      this.apiPath = "/workflow/method/access";
      this.defaultAddMask = 2;
      this.bitsCount = 3;
    }
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.load().then(() => this.onSelect());
  }
  componentDidUpdate(prevProps) {
    if (prevProps.itemId !== this.props.itemId) {
      this.load();
    }
  }

  load = async () => {
    const { itemId } = this.props;
    if (!this.apiPath || !itemId) return false;
    this.pendingStart();
    let items = await this.context.api.q(
      this.apiPath,
      {
        id: itemId,
      },
      { toArray: true }
    );
    this.pendingEnd();
    this.setState({ items });
  };

  maskMakeB = (mask) => {
    let result = mask.toString(2);
    while (result.length < this.bitsCount) result = "0" + result;
    return result;
  };

  save = async (data) => {
    let { item } = this.state;
    let { mode, itemId } = this.props;
    let rights = accessRights[mode] || false;
    if (item.username && rights) {
      let resultMask = {};
      ["allow", "deny"].forEach((grant) => {
        resultMask[grant] = 0;
        Object.entries(rights[grant]).forEach(([level, valueMask]) => {
          let elem = document.getElementById(`${grant}_${level}`) || {};
          if (elem.checked) {
            resultMask[grant] = resultMask[grant] | valueMask;
          }
        });
      });

      let mask = parseInt(
        this.maskMakeB(resultMask.deny) + this.maskMakeB(resultMask.allow),
        2
      );

      let checkRecursive = document.getElementById(`recursive`) || {};
      let checkObjectset = document.getElementById(`objectset`) || {};

      let saveData = {
        id: itemId,
        userid: item.userid,
        mask,
      };

      if (mode === "class") {
        saveData.recursive = checkRecursive.checked ? true : false;
        saveData.objectset = checkObjectset.checked ? true : false;
      }

      this.pendingStart();
      let result = await this.context.api.q(this.apiPath + "/set", saveData);

      if (result !== false) {
        await this.load();
        this.onSelect(item.username);
        message.success(this.m("save.success"));
      } else {
        message.error(this.m("save.fail"));
        this.pendingEnd();
      }
    }
  };

  onDelete = async (item) => {
    const { itemId } = this.props;
    this.pendingStart();

    let result = await this.context.api.q(this.apiPath + "/set", {
      id: itemId,
      userid: item.userid,
      mask: 0,
    });
    if (result !== false) {
      await this.load();
      this.onSelect();
      message.success(this.m("save.success"));
    } else {
      message.error(this.m("save.fail"));
      this.pendingEnd();
    }
  };

  onPick = async (item) => {
    const { itemId } = this.props;
    if (item) {
      this.onAddEnd();
      this.pendingStart();
      let result = await this.context.api.q(this.apiPath + "/set", {
        id: itemId,
        userid: item.id,
        mask: this.defaultAddMask,
      });
      if (result !== false) {
        await this.load();
        this.onSelect(item.username);
        message.success(this.m("save.success"));
      } else {
        message.error(this.m("save.fail"));
      }
      this.pendingEnd();
    }
  };

  onAddEnd = () => {
    this.setState({ pick: false });
  };
  onAddStart = () => {
    this.setState({ pick: true });
  };

  onSelect = (username) => {
    const { items } = this.state;
    let item = {};
    if (username) {
      item = items.find((item) => item.username === username);
    } else {
      item = items[0];
    }
    this.setState({ item: item || {} });
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.workflow.security." + key });
  };

  renderRights() {
    const { item } = this.state;
    const { intl, mode } = this.props;

    let rights = accessRights[mode] || false;

    const Checkbox = ({ level, label, grant }) => (
      <Space>
        <input
          defaultChecked={rights[grant][level] & item[grant] ? true : false}
          type="checkbox"
          name={`${grant}_${level}`}
          id={`${grant}_${level}`}
        />

        {label}
      </Space>
    );

    if (item.username && rights) {
      if (this.props.mode === "class")
        return (
          <>
            <hr />
            <Space direction="vertical" style={{ width: "100%" }}>
              <Alert type="info" message={item.name} />
              <Row style={{ marginTop: "15px" }}>
                <Col span={12}>
                  <Space direction="vertical">
                    <Text type="secondary">{this.m("disallow")}</Text>
                    <Checkbox
                      grant="deny"
                      level="ACCESS"
                      label={this.m("access")}
                    />
                    <Checkbox
                      grant="deny"
                      level="CREATE"
                      label={this.m("create")}
                    />
                    <Checkbox
                      grant="deny"
                      level="SELECT"
                      label={this.m("select")}
                    />
                    <Checkbox
                      grant="deny"
                      level="UPDATE"
                      label={this.m("update")}
                    />
                    <Checkbox
                      grant="deny"
                      level="DELETE"
                      label={this.m("delete")}
                    />
                  </Space>
                </Col>
                <Col span={12}>
                  <Space direction="vertical">
                    <Text type="secondary">{this.m("allow")}</Text>
                    <Checkbox
                      grant="allow"
                      level="ACCESS"
                      label={this.m("access")}
                    />
                    <Checkbox
                      grant="allow"
                      level="CREATE"
                      label={this.m("create")}
                    />
                    <Checkbox
                      grant="allow"
                      level="SELECT"
                      label={this.m("select")}
                    />
                    <Checkbox
                      grant="allow"
                      level="UPDATE"
                      label={this.m("update")}
                    />
                    <Checkbox
                      grant="allow"
                      level="DELETE"
                      label={this.m("delete")}
                    />
                  </Space>
                </Col>
              </Row>
              <div style={{ marginTop: "15px" }}>
                <Space direction="vertical">
                  <Button
                    type="primary"
                    loading={this.state.savePending}
                    onClick={this.save}
                  >
                    {intl.formatMessage({
                      id: "save",
                    })}
                  </Button>

                  <label className="checkbox  mt-4">
                    <Space>
                      <input
                        defaultChecked={false}
                        type="checkbox"
                        name={`recursive`}
                        id={`recursive`}
                      />
                      {this.m("recuresive")}
                    </Space>
                  </label>

                  <label className="checkbox  mt-2">
                    <Space>
                      <input
                        defaultChecked={false}
                        type="checkbox"
                        name={`objectset`}
                        id={`objectset`}
                      />
                      {this.m("objectset")}
                    </Space>
                  </label>
                </Space>
              </div>
            </Space>
          </>
        );
      if (this.props.mode === "method")
        return (
          <>
            <hr />
            <Alert type="info" message={item.name} />
            <Row style={{ marginTop: "15px" }}>
              <Col span={12}>
                <Space direction="vertical">
                  <Text type="secondary">{this.m("disallow")}</Text>
                  <Checkbox
                    grant="deny"
                    level="EXECUTE"
                    label={this.m("execute")}
                  />
                  <Checkbox
                    grant="deny"
                    level="VISIBLE"
                    label={this.m("visible")}
                  />
                  <Checkbox
                    grant="deny"
                    level="ENABLE"
                    label={this.m("enable")}
                  />
                </Space>
              </Col>
              <Col span={12}>
                <Space direction="vertical">
                  <Text type="secondary">{this.m("allow")}</Text>
                  <Checkbox
                    grant="allow"
                    level="EXECUTE"
                    label={this.m("execute")}
                  />
                  <Checkbox
                    grant="allow"
                    level="VISIBLE"
                    label={this.m("visible")}
                  />
                  <Checkbox
                    grant="allow"
                    level="ENABLE"
                    label={this.m("enable")}
                  />
                </Space>
              </Col>
            </Row>
            <div style={{ marginTop: "15px" }}>
              <Space>
                <Button
                  type="primary"
                  loading={this.state.savePending}
                  onClick={this.save}
                >
                  {intl.formatMessage({
                    id: "save",
                  })}
                </Button>
                {this.props.onCancell ? (
                  <Button onClick={this.props.onCancell}>
                    {intl.formatMessage({
                      id: "close",
                    })}
                  </Button>
                ) : null}
              </Space>
            </div>
          </>
        );
    }
  }

  render() {
    const { pending, items, pick, item } = this.state;
    const { intl } = this.props;

    const columns = [
      {
        title: this.m("name"),
        dataIndex: "name",
        key: "name",
        render: (value, item) => (
          <Space>
            {item.type === "U" ? <UserOutlined /> : <UsergroupAddOutlined />}
            {value}
          </Space>
        ),
      },
      {
        title: (
          <Flex between>
            <div>{this.m("description")}</div>
            <Space>
              <Button
                onClick={() => this.onAddStart()}
                icon={<PlusOutlined />}
              />
            </Space>
          </Flex>
        ),

        dataIndex: "description",
        key: "description",
        render: (value, item) => (
          <Flex between>
            <div>{value}</div>
            <Space>
              <Popconfirm
                title={`${intl.formatMessage({
                  id: "delete.sure",
                })} ${item.name}`}
                onConfirm={() => this.onDelete(item)}
                okText={intl.formatMessage({ id: "yes" })}
                cancelText={intl.formatMessage({ id: "no" })}
              >
                <Button icon={<DeleteOutlined />} />
              </Popconfirm>
            </Space>
          </Flex>
        ),
      },
    ];

    return (
      <>
        {pick && (
          <UserGroupPicker onCancel={this.onAddEnd} onPick={this.onPick} />
        )}
        <Spin spinning={pending ? true : false}>
          <Space direction="vertical" style={{ width: "100%" }}>
            <Table
              columns={columns}
              dataSource={items}
              rowClassName={(record) =>
                record.username === item.username ? "row-selected" : ""
              }
              pagination={{ hideOnSinglePage: true }}
              onRow={(record) => {
                return {
                  onClick: () => this.onSelect(record.username),
                };
              }}
            />
            {this.renderRights()}
          </Space>
        </Spin>
      </>
    );
  }
}
export default withRouter(injectIntl(WorkflowSecurity));
