import React from "react";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import { Typography } from "antd";
import WorkflowConstructorStates from "./WorkflowConstructorStates";
import WorkflowConstructorMethods from "./WorkflowConstructorMethods";
import WorkflowConstructorTransitions from "./WorkflowConstructorTransitions";
import WorkflowConstructorEvents from "./WorkflowConstructorEvents";
const { Title } = Typography;

class WorkflowConstructor extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      state: {},
      action: {},
      transition: {},
    };
  }

  onStateChanged = (state) => {
    this.setState({ state });
  };
  onActionChanged = (action) => {
    this.setState({ action });
  };
  onTransitionChanged = (transition) => {
    this.setState({ transition });
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.workflow.constructor." + key });
  };

  render() {
    const { classId } = this.props;
    return (
      <>
        <Title level={5}>{this.m("states")}</Title>
        <WorkflowConstructorStates
          onChange={this.onStateChanged}
          classId={classId}
          key={classId}
        />
        <Title style={{ marginTop: "25px" }} level={5}>
          {this.m("methods")}
        </Title>
        <WorkflowConstructorMethods
          onChange={this.onActionChanged}
          classId={this.props.classId}
          stateId={this.state.state.id}
          stateItem={this.state.state}
          key={this.state.state.id}
        />

        <Title style={{ marginTop: "25px" }} level={5}>
          {this.m("transitions")}
        </Title>
        <WorkflowConstructorTransitions
          onChange={this.onTransitionChanged}
          classId={this.props.classId}
          stateId={this.state.state.id}
          stateItem={this.state.state}
          actionId={this.state.action.id}
          actionItem={this.state.action}
          key={`transitions${this.state.state.id}${this.state.action.id}`}
        />
        <Title style={{ marginTop: "25px" }} level={5}>
          {this.m("events")}
        </Title>
        <WorkflowConstructorEvents
          onChange={() => {}}
          classId={this.props.classId}
          stateId={this.state.state.id}
          stateItem={this.state.state}
          actionId={this.state.action.id}
          actionItem={this.state.action}
          key={`events${this.state.state.id}${this.state.action.id}`}
        />
      </>
    );
  }
}
export default injectIntl(WorkflowConstructor);
