import React from "react";
import key from "../../../util/key";
import Context from "../../../Context";

import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import {
  Form,
  Input,
  Button,
  Space,
  Spin,
  Checkbox,
  message,
  Alert,
} from "antd";

import PlainSelect from "../../../util/PlainSelect";
import TreeSelect from "../../../util/TreeSelect";

class WorkflowClassEdit extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      formKey: key(),
      savePending: false,
      pending: false,
      item: {},
      notFound: false,
    };
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.load();
  }
  componentDidUpdate(prevProps) {
    if (prevProps.itemId !== this.props.itemId) {
      this.load();
    }
  }

  load = async () => {
    const { itemId } = this.props;
    const formKey = key();
    let notFound = false;

    //Load item, has id
    if (itemId && itemId !== "add") {
      this.pendingStart();
      let item = {};
      let itemQuery = await this.context.api.q("/workflow/class/get", {
        id: this.props.itemId,
      });

      if (itemQuery === false) {
        notFound = true;
      } else {
        item = itemQuery;
      }
      this.setState({ item, notFound, formKey }, () => this.pendingEnd());
    }

    //Sets default item, add mode
    if (itemId === "add") {
      this.setState({ item: {}, notFound, formKey });
    }
  };

  save = async (data) => {
    this.setState({ savePending: true });
    const { itemId, intl } = this.props;

    //Prepare save data & perform query
    let saveData = {
      ...data,
      id: !itemId || itemId === "add" ? null : itemId,
    };
    let result = await this.context.api.q("/workflow/class/set", saveData);
    this.setState({ savePending: false });

    //Check result
    if (result === false) {
      //Error...
      message.error(intl.formatMessage({ id: "save.fail" }));
    } else {
      //Success...
      message.success(intl.formatMessage({ id: "save.success" }));

      //Call higher reload if present
      if (this.props.onChange) {
        this.props.onChange();
      }
      if (itemId === "add" && result.id) {
        //Redirect after save if it was new item
        this.props.history.push(`/admin/workflow/${result.id}/edit`);
      } else {
        //...or reload if item was existing
        this.load();
      }
    }
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.workflow.edit." + key });
  };

  render() {
    const { savePending, pending, notFound, item, formKey } = this.state;
    const { intl } = this.props;
    if (notFound) {
      return (
        <Alert
          type="error"
          message={intl.formatMessage({ id: "nothingFound" })}
        />
      );
    }

    return (
      <>
        <Spin spinning={pending ? true : false}>
          <Form
            key={formKey}
            layout="vertical"
            name="profile"
            initialValues={{
              label: item.label || "",
              code: item.code || "",
              parent: item.parent || "",
              entity: item.entity || "",
              abstract: item.abstract || false,
            }}
            onFinish={this.save}
            onFinishFailed={this.onFormFinishFailed}
          >
            <Space direction="vertical" style={{ width: "100%" }}>
              <Form.Item
                rules={[{ required: true }]}
                label={this.m("label")}
                name="label"
              >
                <Input />
              </Form.Item>
              <Form.Item
                rules={[{ required: true }]}
                label={this.m("code")}
                name="code"
              >
                <Input />
              </Form.Item>
              <Form.Item
                rules={[{ required: true }]}
                label={this.m("parent")}
                name="parent"
              >
                <TreeSelect treeDefaultExpandAll query="/workflow/class/list" />
              </Form.Item>
              <Form.Item
                rules={[{ required: true }]}
                label={this.m("entity")}
                name="entity"
              >
                <PlainSelect labelKey="name" query="/workflow/entity/list" />
              </Form.Item>
              <Form.Item name="abstract" valuePropName="checked">
                <Checkbox>{this.m("abstract")}</Checkbox>
              </Form.Item>

              <Form.Item>
                <Button loading={savePending} type="primary" htmlType="submit">
                  {intl.formatMessage({
                    id: "save",
                  })}
                </Button>
              </Form.Item>
            </Space>
          </Form>
        </Spin>
      </>
    );
  }
}
export default withRouter(injectIntl(WorkflowClassEdit));
