import React from "react";
import key from "../../../util/key";
import Context from "../../../Context";
import Flex from "../../../util/Flex";
import { EditOutlined, DeleteOutlined, PlusOutlined } from "@ant-design/icons";

import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import {
  Form,
  Input,
  Button,
  Space,
  Spin,
  message,
  Alert,
  Table,
  Popconfirm,
  Typography,
  Modal,
} from "antd";

const { Text } = Typography;

class WorkflowTypes extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      formKey: key(),
      savePending: false,
      pending: false,
      notFound: false,
      modal: false,
      types: [],
      item: {},
    };
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.load();
  }
  componentDidUpdate(prevProps) {
    if (prevProps.itemId !== this.props.itemId) {
      this.load();
    }
  }

  load = async () => {
    const { itemId } = this.props;

    if (itemId) {
      this.pendingStart();
      let types = await this.context.api.q(
        "/workflow/type/list",
        {
          search: [{ field: "class", compare: "EQL", value: itemId }],
        },
        { toArray: true }
      );

      this.setState({ types }, () => this.pendingEnd());
    }
  };

  save = async (data) => {
    this.setState({ savePending: true });
    const { intl, itemId } = this.props;
    const { item } = this.state;

    //Prepare save data & perform query
    let saveData = {
      ...data,
      class: itemId,
      id: !item.id ? null : item.id,
    };

    let result = await this.context.api.q("/workflow/type/set", saveData);
    this.setState({ savePending: false });

    //Check result
    if (result === false) {
      //Error...
      message.error(intl.formatMessage({ id: "save.fail" }));
    } else {
      //Success...
      message.success(intl.formatMessage({ id: "save.success" }));
      this.onEditEnd();
      this.load();
    }
  };

  onDelete = async (item) => {
    const { intl } = this.props;
    this.pendingStart();

    let result = await this.context.api.q("/workflow/type/delete", {
      id: item.id,
    });
    if (result !== false) {
      message.success(intl.formatMessage({ id: "delete.success" }));
      this.load();
    } else {
      message.error(intl.formatMessage({ id: "delete.fail" }));
      this.pendingEnd();
    }
  };
  onEditStart = (item) => {
    this.setState({ item, formKey: key(), modal: true });
  };
  onEditEnd = () => {
    this.setState({ modal: false });
  };
  onAddStart = () => {
    this.setState({ item: {}, formKey: key(), modal: true });
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.workflow.types." + key });
  };

  render() {
    const {
      savePending,
      pending,
      notFound,
      types,
      item,
      formKey,
      modal,
    } = this.state;
    const { intl } = this.props;
    if (notFound) {
      return (
        <Alert
          type="error"
          message={intl.formatMessage({ id: "nothing.found" })}
        />
      );
    }

    const columns = [
      {
        title: this.m("code"),
        dataIndex: "code",
        key: "code",
        render: (value) => <Text type="secondary">{value}</Text>,
      },
      {
        title: this.m("name"),
        dataIndex: "name",
        key: "name",
      },
      {
        title: (
          <Flex between>
            <div>{this.m("description")}</div>
            <Space>
              <Button
                onClick={() => this.onAddStart()}
                icon={<PlusOutlined />}
              />
            </Space>
          </Flex>
        ),

        dataIndex: "description",
        key: "description",
        render: (value, item) => (
          <Flex between>
            <div>{value}</div>
            <Space>
              <Button
                onClick={() => this.onEditStart(item)}
                icon={<EditOutlined />}
              />
              <Popconfirm
                title={`${intl.formatMessage({
                  id: "delete.sure",
                })} ${item.name}`}
                onConfirm={() => this.onDelete(item)}
                okText={intl.formatMessage({ id: "yes" })}
                cancelText={intl.formatMessage({ id: "no" })}
              >
                <Button icon={<DeleteOutlined />} />
              </Popconfirm>
            </Space>
          </Flex>
        ),
      },
    ];

    return (
      <>
        <Spin spinning={pending ? true : false}>
          <Modal onCancel={this.onEditEnd} visible={modal} footer={null}>
            <Form
              key={formKey}
              layout="vertical"
              name="type"
              initialValues={{
                name: item.name || "",
                code: item.code || "",
                description: item.description || "",
              }}
              onFinish={this.save}
              onFinishFailed={this.onFormFinishFailed}
            >
              <Space direction="vertical" style={{ width: "100%" }}>
                <Form.Item
                  rules={[{ required: true }]}
                  label={this.m("name")}
                  name="name"
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  rules={[{ required: true }]}
                  label={this.m("code")}
                  name="code"
                >
                  <Input />
                </Form.Item>
                <Form.Item label={this.m("description")} name="description">
                  <Input />
                </Form.Item>

                <Form.Item style={{ marginTop: "10px" }}>
                  <Space>
                    <Button
                      className="gx-mb-0"
                      loading={savePending}
                      type="primary"
                      htmlType="submit"
                    >
                      {intl.formatMessage({
                        id: "save",
                      })}
                    </Button>
                    <Button onClick={this.onEditEnd} className="gx-mb-0">
                      {intl.formatMessage({
                        id: "cancell",
                      })}
                    </Button>
                  </Space>
                </Form.Item>
              </Space>
            </Form>
          </Modal>
          <Space direction="vertical" style={{ width: "100%" }}>
            <Table columns={columns} dataSource={types} />
          </Space>
        </Spin>
      </>
    );
  }
}
export default withRouter(injectIntl(WorkflowTypes));
