import React from "react";
import { injectIntl } from "react-intl";
import key from "../../../util/key";
import Layout4 from "../../../util/Layout4";
import { Tree, Tabs, Popconfirm, Space, Tooltip, Button, message } from "antd";
import Context from "../../../Context";
import { withRouter } from "react-router-dom";

import paramsHasChanged from "../../../util/paramsHasChanged";
import {
  PlusOutlined,
  DeleteOutlined,
  PartitionOutlined,
} from "@ant-design/icons";
import WorkflowClassEdit from "./WorkflowClassEdit";
import WorkflowTypes from "./WorkflowTypes";
import WorkflowSecurity from "./WorkflowSecurity";
import WorkflowConstructor from "./WorkflowConstructor";
import Title from "../Title";

const { TabPane } = Tabs;

class Workflow extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      treeSearch: "",
      treeKey: key(),
      tree: [],
    };
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.setParams();
    this.loadTree();
  }

  componentDidUpdate(prevProps) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.loadTree();
    }
  }

  setParams = () => {
    const {
      param1: classId,
      param2: tabKey = "edit",
    } = this.props.match.params;
    this.setState({
      classId,
      tabKey,
    });
  };

  loadTree = async () => {
    this.setState({ pending: true });
    let search = [];
    if (this.state.treeSearch) {
      search.push({
        field: "label",
        compare: "IKE",
        value: `%${this.state.treeSearch}%`,
      });
    }
    let tree = await this.context.api.q(
      "/workflow/class/list",
      { search },
      { toTree: true }
    );
    this.setState({ tree, treeKey: key(), pending: false });
  };

  onTreeSelect = (selectedKeys) => {
    const { tabKey } = this.state;
    if (Array.isArray(selectedKeys)) {
      if (selectedKeys[0]) {
        this.props.history.push(
          `/admin/workflow/${selectedKeys[0]}/${tabKey || ""}`
        );
      }
    }
  };
  onTreeSearch = (value) => {
    this.setState({ treeSearch: value }, () => this.loadTree());
  };

  onTabChange = (key) => {
    const { classId } = this.state;
    if (key && classId) {
      this.props.history.push(`/admin/workflow/${classId}/${key}`);
    }
  };

  onAddStart = () => {
    this.props.history.push(`/admin/workflow/add`);
  };

  onDelete = async () => {
    const { intl, history } = this.props;
    const { classId } = this.state;
    this.pendingStart();

    let result = await this.context.api.q("/workflow/class/delete", {
      id: classId,
    });
    if (result !== false) {
      message.success(intl.formatMessage({ id: "delete.success" }));
      history.push(`/admin/workflow/`);
      this.loadTree();
    } else {
      message.error(intl.formatMessage({ id: "delete.fail" }));
      this.pendingEnd();
    }
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.workflow." + key });
  };

  render() {
    const { pending, treeKey, tabKey, classId } = this.state;

    const { intl } = this.props;
    return (
      <>
        <Title
          title={this.m("workflow")}
          url="/admin/workflow"
          icon={<PartitionOutlined />}
        />
        <Layout4 pending={pending} onSearch={this.onTreeSearch}>
          <div></div>
          <div className="isoNoteBtns">
            <Space>
              <Tooltip title={this.m("btn.add")}>
                <Button
                  onClick={this.onAddStart}
                  type="primary"
                  icon={<PlusOutlined />}
                />
              </Tooltip>
              <Popconfirm
                title={`${intl.formatMessage({
                  id: "delete.sure",
                })}`}
                onConfirm={() => this.onDelete()}
                okText={intl.formatMessage({ id: "yes" })}
                cancelText={intl.formatMessage({ id: "no" })}
              >
                <Tooltip title={this.m("btn.delete")}>
                  <Button icon={<DeleteOutlined />} />
                </Tooltip>
              </Popconfirm>
            </Space>
          </div>

          <Tree
            key={treeKey}
            defaultExpandAll
            defaultExpandParent
            defaultSelectedKeys={[classId]}
            defaultExpandedKeys={[classId]}
            treeData={this.state.tree}
            onSelect={this.onTreeSelect}
          />

          <div>
            {tabKey && classId && (
              <Tabs defaultActiveKey={tabKey} onChange={this.onTabChange}>
                <TabPane
                  tab={intl.formatMessage({ id: "admin.workflow.tab.edit" })}
                  key="edit"
                >
                  <WorkflowClassEdit
                    itemId={classId}
                    onChange={this.loadTree}
                    onPendingStart={this.pendingStart}
                    onPendingEnd={this.pendingEnd}
                  />
                </TabPane>
                {classId !== "add" && (
                  <>
                    <TabPane
                      tab={intl.formatMessage({
                        id: "admin.workflow.tab.workflow",
                      })}
                      key="workflow"
                    >
                      <WorkflowConstructor classId={classId} />
                    </TabPane>
                    <TabPane
                      tab={intl.formatMessage({
                        id: "admin.workflow.tab.types",
                      })}
                      key="types"
                    >
                      <WorkflowTypes
                        itemId={classId}
                        onPendingStart={this.pendingStart}
                        onPendingEnd={this.pendingEnd}
                      />
                    </TabPane>
                    <TabPane
                      tab={intl.formatMessage({
                        id: "admin.workflow.tab.security",
                      })}
                      key="security"
                    >
                      <WorkflowSecurity
                        itemId={classId}
                        onPendingStart={this.pendingStart}
                        onPendingEnd={this.pendingEnd}
                        mode="class"
                      />
                    </TabPane>
                  </>
                )}
              </Tabs>
            )}
          </div>
        </Layout4>
      </>
    );
  }
}

export default withRouter(injectIntl(Workflow));
