import React from "react";
import { injectIntl } from "react-intl";
import { TreeSelect } from "antd";
import Context from "../Context";

class FTreeSelect extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      searchText: "",
      pending: false,
      items: [],
    };
  }
  componentDidMount() {
    this.load();
  }
  load = async () => {
    if (this.props.items) {
      this.setState({ items: this.props.items });
      return;
    }

    if (this.props.query) {
      this.setState({ pending: true });
      let search = [];
      if (this.state.searchText && this.props.async) {
        search.push({
          field: this.props.labelKey || "label",
          compare: "IKE",
          value: `%${this.state.searchText}%`,
        });
      }
      let items = await this.context.api.q(
        this.props.query,
        { search },
        { toTree: true, parentKey: this.props.parentKey }
      );
      this.setState({ pending: false, items });
    }
  };
  onSearch = async (value) => {
    if (this.props.async) {
      this.setState({ searchText: value }, () => this.load());
    }
  };
  render() {
    return (
      <TreeSelect
        allowClear={this.props.allowClear}
        loading={this.state.pending ? true : false}
        value={!this.state.pending ? this.props.value : null}
        treeDefaultExpandAll={this.props.treeDefaultExpandAll}
        showSearch
        placeholder={this.props.placeholder}
        treeNodeFilterProp="children"
        onChange={this.props.onChange}
        onSearch={this.onSearch}
        treeData={this.state.items}
        filterTreeNode={(input, option) =>
          option.title.toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
      />
    );
  }
}
export default injectIntl(FTreeSelect);
