import React from "react";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import Flex from "../../../util/Flex";
import WorkflowSecurity from "./WorkflowSecurity";
import {
  EditOutlined,
  DeleteOutlined,
  PlusOutlined,
  UnorderedListOutlined,
  SecurityScanOutlined,
} from "@ant-design/icons";
import PlainSelect from "../../../util/PlainSelect";
import {
  Form,
  Input,
  Button,
  Space,
  Spin,
  message,
  Alert,
  Table,
  Popconfirm,
  Typography,
  Modal,
} from "antd";

const { Text } = Typography;

class WorkflowConstructorMethods extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      all: false,
      pending: false,
      activeItem: false,
      add: false,
      edit: false,
      security: false,
      items: {},
    };
    this.apiPath = "/workflow/method";
  }

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({
      id: "admin.workflow.constructor.methods." + key,
    });
  };

  componentDidMount() {
    this.load();
  }

  load = async () => {
    if (this.props.classId) {
      if (!this.props.stateId && !this.state.all) return;
      this.setState({ pending: true });

      let search = [
        { field: "class", compare: "EQL", value: this.props.classId },
      ];

      if (this.props.stateId && !this.state.all)
        search.push({
          field: "state",
          compare: "EQL",
          value: this.props.stateId,
        });

      let items = await this.context.api.q(
        this.apiPath + "/list",
        {
          search,
          orderby: ["sequence ASC"],
        },
        { toObject: true }
      );

      this.setState({ pending: false, items }, () =>
        this.select(this.state.activeItem)
      );
    }
  };

  select = (id = false) => {
    if (!id) {
      for (let nid in this.state.items) {
        id = nid;
        break;
      }
    }

    this.setState({ activeItem: String(id) });
    let activeItem = this.state.items[String(id)] || {};
    this.props.onChange(activeItem);
  };

  save = async (data) => {
    const { intl } = this.props;
    let activeItem = this.state.items[this.state.activeItem];
    if (this.state.edit) data.id = activeItem["id"];
    data.class = this.props.classId;
    data.state = this.props.stateId;

    if (!data.sequence) delete data.sequence;

    this.setState({ pending: true });
    let result = await this.context.api.q(this.apiPath + "/set", data);
    if (result !== false) {
      message.success(intl.formatMessage({ id: "save.success" }));
      await this.load();
      this.setState({ edit: false, add: false, security: false });
      if (result["id"]) this.select(result["id"]);
    } else {
      message.error(intl.formatMessage({ id: "save.fail" }));
    }
    this.setState({ pending: false });
  };

  delete = async (item = {}) => {
    const { intl } = this.props;
    this.setState({ pending: true });

    let result = await this.context.api.q(this.apiPath + "/delete", {
      id: item.id,
    });
    if (result !== false) {
      message.success(intl.formatMessage({ id: "delete.success" }));
      this.select();
      this.load();
    } else {
      message.error(intl.formatMessage({ id: "delete.fail" }));
    }

    this.setState({ pending: false });
  };

  toggleAll = () => {
    this.setState(
      (state) => {
        return { all: !state.all, activeItem: false };
      },
      () => this.load()
    );
  };

  onEditStart = (item) => {
    this.setState({ edit: true, activeItem: item.id });
  };
  onSecurityStart = (item) => {
    this.setState({ security: true, activeItem: item.id });
  };
  onAddStart = () => {
    this.setState({ add: true, activeItem: false });
  };
  onEditEnd = () => {
    this.setState({ add: false, edit: false, security: false });
  };

  renderPath() {
    let activeItem = this.state.items[this.state.activeItem] || {};
    let classlabel = activeItem.classlabel || this.props.stateItem.classlabel;
    let statelabel = activeItem.statelabel || this.props.stateItem.label;
    return (
      <Alert
        style={{ marginBottom: "15px" }}
        type="info"
        message={
          <Space>
            {classlabel}
            <span className="fa fa-long-arrow-right"></span>
            {statelabel}
          </Space>
        }
      />
    );
  }

  render() {
    const { classId, intl } = this.props;
    let activeItem = this.state.items[this.state.activeItem] || {};
    const columns = [
      {
        title: this.m("actioncode"),
        dataIndex: "actioncode",
        key: "actioncode",
        render: (value) => <Text type="secondary">{value}</Text>,
      },
      {
        title: this.m("actionname"),
        dataIndex: "actionname",
        key: "actionname",
      },
      {
        title: this.m("code"),
        dataIndex: "code",
        key: "code",
        render: (value) => <Text type="secondary">{value}</Text>,
      },
      {
        title: (
          <Flex between>
            <div>{this.m("label")}</div>
            <Space>
              <Button
                type={this.state.all ? "primary" : null}
                onClick={() => this.toggleAll()}
                icon={<UnorderedListOutlined />}
              />
              <Button
                onClick={() => this.onAddStart()}
                icon={<PlusOutlined />}
              />
            </Space>
          </Flex>
        ),
        dataIndex: "label",
        key: "label",
        render: (value, item) => (
          <Flex between>
            <div>{value}</div>
            <Space>
              <Button
                onClick={() => this.onSecurityStart(item)}
                icon={<SecurityScanOutlined />}
              />
              <Button
                onClick={() => this.onEditStart(item)}
                icon={<EditOutlined />}
              />
              <Popconfirm
                title={`${intl.formatMessage({
                  id: "delete.sure",
                })} ${value}`}
                onConfirm={() => this.delete(item)}
                okText={intl.formatMessage({ id: "yes" })}
                cancelText={intl.formatMessage({ id: "no" })}
              >
                <Button icon={<DeleteOutlined />} />
              </Popconfirm>
            </Space>
          </Flex>
        ),
      },
    ];
    return (
      <>
        <Spin spinning={this.state.pending ? true : false}>
          {classId && (
            <>
              <Table
                pagination={{ hideOnSinglePage: true }}
                columns={columns}
                dataSource={Object.values(this.state.items)}
                rowClassName={(record) =>
                  record.id === activeItem.id ? "row-selected" : ""
                }
                onRow={(record) => {
                  return {
                    onClick: () => this.select(record.id),
                  };
                }}
              />
            </>
          )}
        </Spin>
        <Modal
          onCancel={this.onEditEnd}
          visible={this.state.security}
          footer={null}
          width="800px"
        >
          <WorkflowSecurity
            onCancell={() =>
              this.setState({
                add: false,
                edit: false,
                security: false,
              })
            }
            mode="method"
            itemId={activeItem.id}
          />
        </Modal>
        <Modal
          onCancel={this.onEditEnd}
          visible={this.state.add || this.state.edit}
          footer={null}
        >
          {this.renderPath()}
          <Form
            key={activeItem.id}
            layout="vertical"
            name="type"
            initialValues={{
              action: activeItem.action || "",
              sequence: activeItem.sequence || "",
              code: activeItem.code || "",
              label: activeItem.label || "",
            }}
            onFinish={this.save}
            onFinishFailed={this.onFormFinishFailed}
          >
            <Space direction="vertical" style={{ width: "100%" }}>
              <Form.Item
                rules={[{ required: true }]}
                label={this.m("action")}
                name="action"
              >
                <PlainSelect labelKey="name" query="/workflow/action/list" />
              </Form.Item>
              <Form.Item label={this.m("sequence")} name="sequence">
                <Input />
              </Form.Item>
              <Form.Item
                rules={[{ required: true }]}
                label={this.m("code")}
                name="code"
              >
                <Input />
              </Form.Item>
              <Form.Item
                rules={[{ required: true }]}
                label={this.m("label")}
                name="label"
              >
                <Input />
              </Form.Item>

              <Form.Item style={{ marginTop: "10px" }}>
                <Space>
                  <Button
                    className="gx-mb-0"
                    loading={this.state.pending}
                    type="primary"
                    htmlType="submit"
                  >
                    {intl.formatMessage({
                      id: "save",
                    })}
                  </Button>
                  <Button className="gx-mb-0" onClick={this.onEditEnd}>
                    {intl.formatMessage({
                      id: "cancell",
                    })}
                  </Button>
                </Space>
              </Form.Item>
            </Space>
          </Form>
        </Modal>
      </>
    );
  }
}
export default injectIntl(WorkflowConstructorMethods);
