import React from "react";
import { injectIntl } from "react-intl";
import { Modal, Space, Spin, Table, Typography } from "antd";
import { UserOutlined, UsergroupAddOutlined } from "@ant-design/icons";
import Context from "../Context";
import Searchbox from "./Searchbox";

const { Text } = Typography;

class UserGroupPicker extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      search: "",
      pending: false,
      items: [],
    };
  }

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "usergrouppicker." + key });
  };

  componentDidMount() {
    this.load();
  }

  load = async () => {
    this.setState({ pending: true });
    let search = [];
    if (this.state.search) {
      search.push({
        lstr: "(",
        field: "name",
        compare: "IKE",
        value: `%${this.state.search}%`,
      });
      search.push({
        condition: "OR",
        field: "username",
        compare: "IKE",
        value: `%${this.state.search}%`,
        rstr: ")",
      });
    }
    let groups = await this.context.api.q(
      "/admin/group/list",
      { search, reclimit: 50 },
      { toArray: true }
    );
    let users = await this.context.api.q(
      "/admin/user/list",
      { search, reclimit: 50 },
      { toArray: true }
    );
    let items = groups.concat(users);
    this.setState({ pending: false, items });
  };

  onSearch = (value) => {
    this.setState({ search: value }, () => this.load());
  };

  render() {
    const { pending, items } = this.state;
    const columns = [
      {
        title: this.m("name"),
        dataIndex: "name",
        key: "name",
        render: (value, item) => (
          <Space>
            {item.created ? <UserOutlined /> : <UsergroupAddOutlined />}
            {value}
          </Space>
        ),
      },
      {
        title: this.m("description"),
        dataIndex: "description",
        key: "description",
        render: (value, item) => (
          <div>
            {item.username !== item.name && <div>item.username</div>}

            <Text type="secondary">{value}</Text>
          </div>
        ),
      },
    ];
    return (
      <Modal
        title={
          <div style={{ paddingRight: "25px" }}>
            <Searchbox onSearch={this.onSearch} />
          </div>
        }
        onCancel={this.props.onCancel}
        visible
        footer={null}
      >
        <Spin spinning={pending ? true : false}>
          <Table
            onRow={(record) => {
              return {
                onDoubleClick: () => {
                  if (this.props.onPick) {
                    this.props.onPick(record);
                  }
                },
              };
            }}
            columns={columns}
            dataSource={items}
          />
        </Spin>
      </Modal>
    );
  }
}
export default injectIntl(UserGroupPicker);
